.icon-angle-left {
  width: 0.59em;
  height: 1em;
  fill: initial;
}

.icon-logout-mark {
  width: 1em;
  height: 1em;
  fill: initial;
}

* {
  font-family: 'Roboto', Arial, sans-serif;
}

body {
  position: relative;
  min-width: 320px;
  color: #333;
  background: #fff;
  font-family: 'Roboto', Arial, sans-serif;
  font-size: 14px;
  line-height: 1.3;
}

.main {
  padding-top: 20px;
  padding-bottom: 20px;
}

.inner {
  position: relative;
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  max-width: 1024px;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

.empty {
  margin-top: 100px;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.3;
  text-align: center;
}

@media only screen and (max-width: 580px) {
  .empty {
    font-size: 24px;
  }
}

@media only screen and (max-width: 480px) {
  .empty {
    font-size: 22px;
  }
}

[hidden] {
  display: none !important;
}

[data-animation*="ripple"] {
  position: relative;
  overflow: hidden;
}

[data-animation*="ripple"] .ripple-circle {
  position: absolute;
  z-index: 20;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  width: 112px;
  height: 112px;
  background: rgba(0, 0, 0, 0.1);
  animation: ripple 0.5s linear forwards;
  pointer-events: none;
}

[data-animation*="white"] .ripple-circle {
  background: rgba(255, 255, 255, 0.1);
}

@keyframes ripple {
  0% {
    opacity: 1;
    transform: scale(0);
  }
  100% {
    opacity: 0;
    transform: scale(2);
  }
}

.btn-sm {
  padding: 10px 20px;
  border-radius: 3px;
  color: #333;
  background: #fff;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.25;
}

.radio {
  position: relative;
  display: inline-block;
  vertical-align: top;
  min-width: 1em;
  min-height: 1em;
  font-size: 30px;
  user-select: none;
}

.radio__input {
  display: none;
}

.radio__input:checked ~ .radio__icon:before {
  opacity: 1;
  transform: scale(1);
}

.radio__input:disabled ~ .radio__icon,
.radio__input:disabled ~ .radio__title {
  opacity: 0.5;
  cursor: not-allowed;
}

.radio__input:disabled ~ .radio__icon:before {
  background: #999999;
}

.radio__icon {
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid #999999;
  border-radius: 50%;
  width: 1em;
  height: 1em;
  cursor: pointer;
}

.radio__icon:before {
  content: '';
  position: absolute;
  top: 15%;
  left: 15%;
  right: 15%;
  bottom: 15%;
  border-radius: 50%;
  opacity: 0;
  background: #2e99db;
  transform: scale(0.5);
  transition: opacity 0.3s, transform 0.3s;
}

.radio__title {
  display: block;
  padding: 5px 0 5px 40px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.25;
  cursor: pointer;
}

.link-more {
  display: inline-block;
  vertical-align: top;
  color: #2e99db;
  text-decoration: underline;
}

.no-touch .link-more:hover {
  text-decoration: none;
}

.link-more.is-active .link-more__hidden {
  display: block;
}

.link-more.is-active .link-more__visible {
  display: none;
}

.link-more__hidden {
  display: none;
}

.textarea {
  display: block;
  padding: 14px 15px;
  border: 1px solid #999999;
  width: 100%;
  height: 150px;
  color: #333;
  background: #fff;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.25;
  resize: none;
  transition: border-color 0.3s;
}

.textarea:focus {
  border-color: #2e99db;
}

.textarea::placeholder {
  color: #cccccc;
  opacity: 1;
}

.select {
  position: relative;
}

.select__field {
  display: block;
  padding: 14px 35px 14px 15px;
  border: 1px solid #999999;
  border-radius: 0;
  width: 100%;
  color: #333;
  background: #fff;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.25;
  resize: none;
  appearance: none;
  transition: border-color 0.3s;
}

.select__field::-ms-expand {
  display: none;
}

.select__field:focus {
  border-color: #2e99db;
}

.select__arrow {
  position: absolute;
  top: 50%;
  right: 15px;
  margin-top: -3px;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 6px 5px 0 5px;
  border-color: #333 transparent transparent transparent;
}

.btn {
  position: relative;
  overflow: hidden;
  display: inline-block;
  vertical-align: top;
  padding: 0 30px;
  min-width: 120px;
  border: none;
  outline: none;
  color: #333;
  background: #e9e9e9;
  font-size: 18px;
  font-weight: 300;
  line-height: 50px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;
  transition: opacity .175s, background .175s, box-shadow .175s;
}

.btn:disabled, .btn.is-disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.btn_primary {
  color: #fff;
  background: #2e99db;
}

.btn_primary:active {
  color: #fff;
  background: #1f7db7;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.btn_red {
  color: #fff;
  background: #ec4a41;
}

.btn_red:active {
  color: #fff;
  background: #e32117;
}

.btn_wide {
  width: 100%;
}

.header {
  position: relative;
  padding-left: 60px;
  padding-right: 60px;
  height: 60px;
  color: #fff;
  background: #2e99db;
  text-align: center;
  font-size: 0;
}

.header:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

@media only screen and (max-width: 767px) {
  .header {
    padding-right: 0;
  }
}

.header__btn-nav {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 60px;
  color: #fff;
  background: none;
  cursor: pointer;
  transition: background 0.25s, box-shadow 0.25s;
}

.no-touch .header__btn-nav:hover {
  background: #1f7db7;
}

.header__btn-nav:active {
  background: #1f7db7;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.header__btn-nav:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -1px 0 0 -14px;
  border-top: 2px solid #fff;
  width: 28px;
}

.header__btn-nav span {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -11px 0 0 -14px;
  width: 28px;
  height: 22px;
}

.header__btn-nav span:before, .header__btn-nav span:after {
  content: '';
  position: absolute;
  left: 0;
  border-top: 2px solid #fff;
  width: 100%;
}

.header__btn-nav span:before {
  top: 0;
}

.header__btn-nav span:after {
  bottom: 0;
}

.header__inner {
  display: inline-flex;
  vertical-align: middle;
  align-items: center;
  padding-top: 5px;
  padding-bottom: 5px;
  text-align: left;
}

@media only screen and (max-width: 580px) {
  .header__inner {
    flex-direction: column;
  }
}

.header__logo, .header__title {
  display: inline-block;
  vertical-align: middle;
}

.header__title {
  font-size: 18px;
  font-weight: 500;
  line-height: 1.2;
  text-transform: uppercase;
}

@media only screen and (max-width: 767px) {
  .header__title {
    font-size: 16px;
  }
}

@media only screen and (max-width: 480px) {
  .header__title {
    font-size: 14px;
  }
}

.header__logo {
  margin-right: 15px;
  width: 80px;
}

@media only screen and (max-width: 480px) {
  .header__logo {
    margin-right: 5px;
    width: 60px;
  }
}

.header__meta {
  padding-left: 20px;
  opacity: 0.8;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
}

@media only screen and (max-width: 767px) {
  .header__meta {
    font-size: 14px;
  }
}

@media only screen and (max-width: 580px) {
  .header__meta {
    display: none;
  }
}

.header__content {
  flex-grow: 1;
}

.nav {
  position: fixed;
  z-index: 200;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  background: rgba(67, 76, 85, 0.8);
  transform: translate3d(0, 0, 0);
  transition: opacity 0.25s, visibility 0.25s;
  transition-delay: 0.2s;
}

.nav.is-open {
  opacity: 1;
  visibility: visible;
  transition-delay: 0s;
}

.nav.is-open .nav__inner {
  transform: translate3d(0, 0, 0);
  transition-delay: 0.2s;
}

.nav.is-open .nav__header,
.nav.is-open .nav__overlay {
  pointer-events: auto;
}

.nav__overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
}

.nav__inner {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  bottom: 0;
  overflow: auto;
  width: 300px;
  color: #fff;
  background: #434c55;
  box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.2);
  transform: translate3d(-100%, 0, 0);
  transition: transform 0.25s ease 0.25s;
  transition-delay: 0s;
}

.nav__header {
  position: relative;
  display: block;
  padding: 0 20px 0 50px;
  width: 100%;
  height: 60px;
  color: #fff;
  background: #2e99db;
  text-align: center;
  font-size: 0;
  text-align: left;
  cursor: pointer;
  pointer-events: none;
  transition: background 0.25s;
}

.nav__header:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.no-touch .nav__header:hover {
  background: #1f7db7;
}

.nav__header:active {
  background: #1f7db7;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.nav__header strong,
.nav__header .icon {
  display: inline-block;
  vertical-align: middle;
}

.nav__header strong {
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.2;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-transform: uppercase;
}

.nav__header .icon {
  position: absolute;
  top: 50%;
  left: 20px;
  margin-top: -0.5em;
  fill: currentColor;
  font-size: 20px;
}

.nav__header span {
  position: absolute;
  left: 50px;
  right: 0;
  bottom: 5px;
  font-size: 10px;
  font-weight: 400;
  line-height: 1;
}

.nav__body {
  padding: 20px;
}

.nav__user-data {
  padding-bottom: 20px;
}

.nav__list {
  margin: 0 -20px;
  border-top: 1px solid #4C5560;
  color: #cccccc;
}

.nav__link {
  position: relative;
  display: block;
  padding: 5px 20px 5px 50px;
  border-bottom: 1px solid #4C5560;
  color: inherit;
  font-size: 16px;
  font-weight: 400;
  line-height: 40px;
  transition: background 0.25s;
}

.no-touch .nav__link:hover {
  background: #4c5560;
}

.nav__link .icon {
  display: block;
  margin: 0 auto;
  fill: currentColor;
}

.nav__icon {
  position: absolute;
  top: 50%;
  left: 5px;
  width: 40px;
  margin-top: -0.5em;
  font-size: 24px;
}

.user-data__title {
