}

.form__footer {
  padding-top: 15px;
  font-size: 0;
  text-align: right;
}

.form__btn:not(:first-child) {
  margin-left: 20px;
}

.form__select {
  margin-top: 30px;
}

@media only screen and (max-width: 767px) {
  .form__select {
    margin-top: 10px;
  }
}

@media only screen and (max-width: 580px) {
  .form__select {
    margin-top: 0;
  }
}

.modal {
  position: fixed;
  z-index: 200;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
  padding: 20px;
  opacity: 0;
  visibility: hidden;
  background: rgba(51, 51, 51, 0.8);
  text-align: center;
  font-size: 0;
  transition: opacity 0.25s, visibility 0.25s;
}

.modal:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.modal.is-open {
  opacity: 1;
  visibility: visible;
}

.modal.is-open .modal__overlay {
  pointer-events: auto;
}

.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
}

.modal__container {
  position: relative;
  z-index: 5;
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  max-width: 400px;
  color: #333;
  background: #fff;
  box-shadow: 0 3px 7px 1px rgba(0, 0, 0, 0.1);
}

.modal__close {
  position: absolute;
  z-index: 2;
  top: 10px;
  right: 10px;
  border-radius: 50%;
  width: 50px;
