.loader {
  position: fixed;
  z-index: 200;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  visibility: hidden;
  background: rgba(255, 255, 255, 0.8);
  text-align: center;
  font-size: 0;
  transition: opacity 0.25s, visibility 0.25s;
}

.loader:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.loader.is-open {
  opacity: 1;
  visibility: visible;
}

.loader__center {
  display: inline-block;
  vertical-align: middle;
}

.loader__title {
  color: #333;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.2;
}

.loader__icon {
  position: relative;
  display: block;
  margin: 0 auto;
  width: 1em;
  height: 1em;
  font-size: 150px;
}

.loader__icon:before, .loader__icon:after {
  content: '';
  position: absolute;
  top: 13%;
  left: 13%;
  right: 13%;
  bottom: 13%;
  border-radius: 50%;
  width: 74%;
  height: 74%;
  background: rgba(0, 0, 0, 0.3);
  transform: scale(0);
  animation: scale 2s cubic-bezier(0.1, 0.2, 0.3, 1) infinite;
}

.loader__icon:after {
  animation-delay: 1s;
}

@keyframes scale {
  0% {
    opacity: 1;
    transform: scale(0);
  }
  90% {
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(1);
  }
}
