.form {
	padding: 20px 0 0;
	&__body {
		margin: 0 -10px;
		font-size: 0; }
	&__col {
		display: inline-block;
		vertical-align: top;
		padding: 10px;
		width: 33.33%;
		@include md {
			width: 50%; }
		@include sm {
			width: 100%; }
		&_md-100 {
			@include md {
				width: 100%; } } }
	&__label {
		margin-bottom: 10px;
		color: lighten($darkGray, 40%);
		@include f(16px, 400, 1.25);
		+ .form__select {
			margin-top: 0; } }
	&__item {
		&:not(:last-child) {
			margin-bottom: 10px; } }
	&__footer {
		padding-top: 15px;
		font-size: 0;
		text-align: right; }
	&__btn {
		&:not(:first-child) {
			margin-left: 20px; } }
	&__select {
		margin-top: 30px;
		@include md {
			margin-top: 10px; }
		@include sm {
			margin-top: 0; } } }
